$navbar-color: #ffffff;
$color-black:#191919;
$color-bblack: #162e2e;
$color-dred:#362e2e;
$color-white:#ffffff;
$color-bgrey:#362e2e;
$color-lgrey:#9d9898;
$color-stryoygurt:#FFEFD9;
$color-red:#78122C; 
$color-honeycomb:#DFDBE5;

.header-title{
    font-family: 'Public Sans', sans-serif;
  }
    
  .sm-head{
    letter-spacing: 0.25rem;
    font-weight: 600;

}
.rg-head{
    letter-spacing: 0.40rem;
    font-weight: 200;
}

.navbar{
    background-color: $navbar-color;
    box-shadow: $navbar-shadow;
    padding: $navbar-padding;
}

.full-content{
  width: 100%;
  height: 92vh;

  .title-content{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
.section{
  width:100%;
  margin: 10px 0 10px 0 ;
  padding-top: 6rem;
  padding-bottom: 6rem;
}
.section:nth-child(even){
    background-color: $color-bblack;
    color: $color-white;
    background-image: $honeyhomb;
    -webkit-clip-path: polygon(0 0,100% 5vw,100% 100%,0 calc(100% - 5vw));
    clip-path: polygon(0 0 , 100% 5vw ,100% 100% , 0 calc(100% - 5vw));
    //clip-path: polygon(0 6rem , 100% 0 , 100% calc(100% - 6rem) , 0 100%);

    .bg-color-dark .nav-link:nth-child(even){
      color: $color-white !important;
      :hover{
        color: #6c757d;
      }
    }
    .neomorph{
      color: $color-black;
      box-shadow: 
      12px 12px 16px 0 rgba(0, 0, 0, 0.15);

     }  
}

.center-content{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.honeycomb{
  background-color:$color-honeycomb;
  background-image: $honeyhomb;
}

.profileImg{
 //background-image: url('../assets/ian.JPG');
 background-repeat: no-repeat;
 background-size: contain;
 display: block;
}

// making landscape images square and cirular
.circular--landscape {
  display: inline-block;
  width: 250px;
  height: 250px;
  overflow: hidden;
  border-radius: 50%;
    
  span, img{
    width: auto;
    height: 100%;
    margin-left: -50px;
  }
}

.circular--img {
  display: inline-block;
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 50%;
 span, img{
    width: auto;
    height: 100%;
    margin-left: -50px;
  }
}

 .img-box{
   display: block;
   background-color: $color-bblack;
   height:150px;
 }

 .neomorph{
  background-color: $navbar-color;
  box-shadow: 
    12px 12px 16px 0 rgba(0, 0, 0, 0.15),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.4);

 }

 .btn-link{
   color: $color-black !important;
   :hover{
    color: $color-lgrey;

   }
 }
 .bottom{
   position: inherit;
   padding-top: 10em;
 }
 .cust-head{
  font-size: 3.5em;
 }